import React, { Component } from "react";
import { Button, Icon } from "antd";
class Animation extends Component {
  render() {
  	const iframeStyle = {
  		width: '100%',
  		height: '100%',
  		border: 'none',
  		margin: 0,
  		padding: 0,
  	}
  	const btnStyle = {
  		position: 'absolute',
  		bottom: 0,
  		left: 0,
  		margin: 20,
  	}
    return (
      <div style={ { width: '100%', height: '100%', position: 'relative' } }>
        <Button type='primary' href="https://github.com/JS-Hao/pixel-cooler" style={btnStyle}>
          <Icon type="github" theme="outlined" />go to github
        </Button>
        <iframe src="https://js-hao.github.io/pixel-cooler/" style={ iframeStyle }></iframe>
      </div>
    );
  }
}

export default Animation;
