import React, { Component } from "react";
import "./App.css";
import "./theme.css";
import Sketchpad from "./components/Sketchpad";
import Animation from "./components/Animation";
import Pixel from "./components/Pixel";

import { Layout, Menu, Breadcrumb, Icon } from "antd";
import { adapter } from './utils/adapter';

const { SubMenu } = Menu;
const { Header, Content, Sider } = Layout;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      menu: 1
    };
  }

  getContent(menu) {
    switch (menu) {
      case 1:
        return <Sketchpad />;

      case 3:
        return <Pixel />;

      case 2:
      default:
        return <Animation />;
    }
  }

  handleClick(menu) {
    this.setState({ menu });
  }

  render() {
    return (
      <Layout>
        <Header className="header">
          <div className="logo" />
          <Menu
            theme="dark"
            mode="horizontal"
            defaultSelectedKeys={["1"]}
            style={{ lineHeight: "64px" }}
          >
            <Menu.Item key="1">个人作品</Menu.Item>
          </Menu>
        </Header>
        <Layout>
          <Sider width={200} style={{ background: "#fff" }}>
            <Menu
              mode="inline"
              defaultSelectedKeys={["1"]}
              defaultOpenKeys={["sub1"]}
              style={{ height: "100%", borderRight: 0 }}
            >
              <Menu.Item key="1" onClick={this.handleClick.bind(this, 1)}>
                <Icon type="experiment" theme="outlined" />
                书写画板
              </Menu.Item>
              <Menu.Item key="2" onClick={this.handleClick.bind(this, 2)}>
                <Icon type="experiment" theme="outlined" />
                组合动画
              </Menu.Item>
              <Menu.Item key="3" onClick={this.handleClick.bind(this, 3)}>
                <Icon type="experiment" theme="outlined" />
                像素动画
              </Menu.Item>
            </Menu>
          </Sider>
          <Layout style={{ padding: "24px", height: "100%" }}>
            <Content
              style={{
                background: "#fff",
                padding: 24,
                margin: 0,
                minHeight: 280,
                height: "100%"
              }}
            >
              {this.getContent(this.state.menu)}
            </Content>
          </Layout>
        </Layout>
      </Layout>
    );
  }
}

export default App;
