import React, { Component } from "react";
import { Button } from "antd";
import Core from "../lib/sketchpad.min";
class Sketchpad extends Component {
  isInit = false;

  componentDidUpdate(prevProps, prevState) {
    if (this.isInit) {
      return;
    }

    this.isInit = true;
    this.core = new Core(this.refs.wrapper);
    this.core.init(this.getParam());
  }

  handleEraser = () => {
    this.core.setTool('eraser');
  }

  handleDrag = () => {
    this.core.setTool('drag');
  }

  handlePen = () => {
    this.core.setTool('pen');
  }

  handleCancel = () => {
    this.core.undo();
  }

  handleClear = () => {
    this.core.clear();
  }

  getParam() {
    return {
      pen: {
        size: 2
      },
      eraser: {
        ico: require('../iamges/eraser.png'),
        w: 38,
        h: 43,
        offsetX: 19,
        offsetY: 21.5,
        type: "rectangle"
      },
      maxStack: 5
    };
  }

  render() {
    const bgStyle = {
      position: "absolute",
      left: 0,
      bottom: 0
    };
    return (
      <div
        style={{
          height: "100%",
          position: "relative"
        }}
      >
        <div
          className="sketchpad-wrapper"
          ref="wrapper"
          style={{
            height: "100%"
          }}
        />
        <Button.Group style={bgStyle}>
          <Button type="primary" onClick={this.handlePen}>
            画笔
          </Button>
          <Button onClick={this.handleEraser}>橡皮</Button>
          <Button onClick={this.handleDrag}>拖拽</Button>
          <Button onClick={this.handleCancel}>撤销</Button>
          <Button onClick={this.handleClear}>清空</Button>
        </Button.Group>
      </div>
    );
  }
}

export default Sketchpad;
